.social-menu-wrapper{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 2rem;
  }

  
.menu-social-menu-container{
    
   #social-menu {
        ul{
            display: flex;
            list-style: none; 
         }
        a {
            content: "";
            position: relative;
            color: transparent;
            display: inline-block;
            width: 32px;
            overflow: hidden;
           
        }
        a:after {
            text-indent: 0;
            display: block;
            float: left;
            font-family: "Font Awesome 5 Brands";
            font-weight: 400;
            visibility: visible;
            font-size: 18px;
            position: absolute;
            top: 0;
        }
        a[href*="twitter.com"] {
            &:after {
                content: "\f099";
                color: #00aced;
            }
        }
        
        a[href*="github.com"] {
            &:after {
                content: "\f09b";
                color: black;
            }
        }
        
        a[href*="linkedin.com"] {
            &:after {
                content: "\f0e1";
                color: #007bb6;
            }
        }
        
        a[href*="instagram.com"] {
            &:after {
                content: "\f16d";
                color: #517fa4;
            }
        }
        
        
        a[href*="facebook.com"] {
            &:after {
                content: "\f39e";
                color: #517fa4;
            }
        }
        
        a[href*="pinterest.com"] {
            &:after {
                content: "\f0d3";
                color: #517fa4;
            }
        }
        a[href*="tumbler.com"] {
            &:after {
                content: "\f174";
                color: #517fa4;
            }
        }
        a[href*="wordpress.com"] {
            &:after {
                content: "\f39e";
                color: #517fa4;
            }
        }
        
        a[href*="wordpress.org"] {
            &:after {
                content: "\f19a";
                color: #517fa4;
            }
        }
        a[href*="youtube.com"] {
            &:after {
                content: "\f431";
                color: #517fa4;
            }
        }
        a[href*="wikipedia.com"] {
            &:after {
                content: "\f266";
                color: #517fa4;
            }
        }
        
        
        a[href$="/feed/"] {
            &:after {
                text-indent: 0;
                display: block;
                float: left;
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
                content: "\f09e";
                color: #ff9900;
            }
        }
    }
    
}

.menu-social-menu-container #social-menu a:after{
    color:#ffffff !important;
    padding: 3px 9px;
    border-radius: 60%;
    position: relative;
  
}

@media only screen and(max-width:992px){
    .search-form{
        display: none;
    }
}

ul#social-menu{
    display: flex;
    list-style: none;
}

